/*dot-flashing*/
.calling_dotFlashing__3_d4l {
  bottom: 3px;
  position: relative;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #009ade;
  color: #009ade;
  -webkit-animation: calling_dotFlashing__3_d4l 1s infinite linear alternate;
          animation: calling_dotFlashing__3_d4l 1s infinite linear alternate;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
  margin-left: 1rem;
}

.calling_dotFlashing__3_d4l::before,
.calling_dotFlashing__3_d4l::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 0;
}

.calling_dotFlashing__3_d4l::before {
  left: -12px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #009ade;
  color: #009ade;
  -webkit-animation: calling_dotFlashing__3_d4l 1s infinite alternate;
          animation: calling_dotFlashing__3_d4l 1s infinite alternate;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.calling_dotFlashing__3_d4l::after {
  left: 12px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #009ade;
  color: #009ade;
  -webkit-animation: calling_dotFlashing__3_d4l 1s infinite alternate;
          animation: calling_dotFlashing__3_d4l 1s infinite alternate;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes calling_dotFlashing__3_d4l {
  0% {
    background-color: #009ade;
  }
  50%,
  100% {
    background-color: #daf2fb;
  }
}

@keyframes calling_dotFlashing__3_d4l {
